import React, { Component } from "react";
import Jumbotron from "react-bootstrap/Jumbotron";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";

export class Footer extends Component {
    state = {
        items: this.props.items,
    };

    componentWillReceiveProps({ items }) {
        this.setState({ items });
    }

    render() {
        const { items } = this.state;

        return (
            <Jumbotron>
                <Row>
                    <Col
                        style={{
                            borderRight: "1px dashed black",
                        }}
                    >
                        <h1>More Info: </h1>
                        <p>
                            I get sources from other pages as mentioned on the
                            top of every card. So please access their pages to
                            read more comics.
                        </p>
                        <p>
                            About @copyrights, please contact:{" "}
                            <b>
                                <i>Duongntse@gmail.com</i>
                            </b>
                        </p>
                    </Col>
                    <Col>
                        <h1>Comic Sites:</h1>
                        {items
                            .reduce((acc, curr, currInd, arr) => {
                                if (acc.length === 0) acc.push(curr);
                                else {
                                    if (
                                        !acc
                                            .map((a) => a.base_site_url)
                                            .includes(curr.base_site_url)
                                    )
                                        acc.push(curr);
                                }
                                return acc;
                            }, [])
                            .map((comic, ind) => (
                                <Button
                                    className={"mx-1"}
                                    key={ind}
                                    target="_blank"
                                    variant="outline-primary"
                                    href={comic.base_site_url}
                                >
                                    {comic.base_site_name}
                                </Button>
                            ))}
                    </Col>
                </Row>
            </Jumbotron>
        );
    }
}

export default Footer;
